.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 4rem;
}

.title-full {
  font-size: 0.8rem;
  color: #3c3c3c;
  display: flex;
  align-items: center;
}

i {
  font-size: 1.4rem;
}

.title {
  letter-spacing: -0.5px;
}

.input {
  border-radius: 1rem;
  outline: none;
  margin-right: 1rem;
  height: 1.6rem;
  width: 20rem;
  padding: 0 0.9em;
  border: 0.2mm solid;
}

button {
  border-radius: 1rem;
  border: none;
  height: 1.9rem;
  width: 5rem;
  cursor: pointer;
}

button:hover {
  background-color: black;
  color: white;
}

.img-container {
  column-count: 3;
  margin: 0 3rem;
}

.img-thumbnail {
  margin-bottom: 0;
  padding-bottom: 0;
  cursor: pointer;
}

.img-profile-container {
  position: relative;
  overflow: hidden;
  margin-bottom: 1rem;
}

.author-info {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0;
  background-color: rgb(234, 234, 234);
  text-align: center;
  transition: height 0.3s ease-out;
  margin-top: 0;
  padding-top: 0;
}

.img-profile-container:hover .author-info {
  height: 40px;
}

.author-profile-name {
  position: relative;
  display: flex;
  align-items: center;
  margin-left: 0.9rem;
  padding: 0.5rem 0;
}

.author-profile-picture {
  border-radius: 50%;
  margin-right: 0.3rem;
  width: 1.5rem;
  height: 1.5rem;
}

.author-name {
  color: #515151;
  text-decoration: none;
  font-size: 0.9rem;
  transition: color 0.3s;
  font-weight: bold;
}

.author-name:hover {
  color: black;
}

.fa-arrow-down {
  font-size: 1rem;
  color: #515151;
  transition: color 0.3s;

  position: absolute;
  top: 0.7rem;
  right: 1rem;
}

.fa-arrow-down:hover {
  color: black;
}

.preview-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 1;
}

.preview-container {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  /* text-align: center; */
}

.preview-image img {
  max-width: 90vw;
  max-height: 90vh;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: #ffffff90;
  color: black;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  height: 1.5rem;
  width: 1.5rem;
  padding: 0 0 0.1rem 0;
  font-size: 1.2rem;
}

.close-button:hover {
  background-color: white;
  color: black;
}

.no-image-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #eee;
  text-align: center;
  height: 600px;
  margin: 0 4rem 1rem;
}

.no-images-copy {
  font-size: 2rem;
  margin-bottom: -0.3rem;
}

.no-images-copy-loading {
  font-size: 3rem;
}

.try-new-search-copy {
  font-size: 1.2rem;
  color: rgb(86, 86, 86);
}

footer {
  margin: 1.5rem;
  text-align: center;
  font-size: 0.7rem;
  font-weight: bold;
}

.fa-code {
  font-size: 0.7rem;
}

.footer-link {
  color:rgb(86, 86, 86)
}

@media screen and (max-width: 831px) {
  .img-container {
    column-count: 2;
  }
  .input {
    width: 20rem;
  }
  .title {
    display: none;
  }
  .header {
    margin: 1rem 0;
  }
}

@media screen and (max-width: 600px) {
  .img-container {
    column-count: 1;
  }
  .input {
    width: 15rem;
  }
  .title {
    display: none;
  }
}

@media screen and (max-width: 520px) {
  .img-container {
    column-count: 1;
  }
  .input {
    width: 12rem;
  }
  .title {
    display: none;
  }
  .header {
    margin: 1rem 0;
  }
}
